import { IInputInfo, ILoginPayload } from '@/types/interface';
import axios from 'axios';

export function getLostPhone(params: IInputInfo): Promise<ILoginPayload> {
  return new Promise((resolve, reject) => {
    axios
      .post('Get_LostPhone', params)
      .then((response) => {
        resolve(response.data);
      })
      .catch((error) => {
        reject(error);
      });
  });
}

